html, body {
  padding: 0; }

body {
  font-family: "proxima-nova", sans-serif;
  font-weight: 400;
  color: #001018;
  font-size: 14px;
  background-color: #f5f5f5;
  -webkit-font-smoothing: antialiased;
  scroll-behaviour: smooth;
  background-color: #111111; }
  @media (min-width: 768px) {
    body {
      font-size: 16px; } }

h1, h2, h3, h4, h5, h6 {
  font-family: "proxima-nova", sans-serif;
  font-weight: 700;
  margin: 0px;
  font-weight: auto; }

a {
  transition: 125ms cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-property: color, opacity, background; }
  a:hover {
    opacity: .7; }

p {
  color: #112236; }

.e-inline-icon {
  width: 0.6rem;
  height: 0.6rem;
  fill: #112236;
  transform: translateY(-50%); }

button {
  background: none;
  border: 0px; }
  button:focus {
    outline: none; }

input:focus {
  outline: none; }

ul {
  padding: 0;
  margin: 0;
  list-style-type: none; }

p {
  margin: 0px; }

a {
  text-decoration: none; }

figure {
  margin: 0;
  padding: 0; }

.lazy-load {
  opacity: 0;
  transition: 0.625s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition-property: opacity, transform;
  will-change: transform, opacity; }
  .lazy-load[data-direction="ltr"] {
    transform: translateY(50px); }
    .lazy-load[data-direction="ltr"].lazy-loaded {
      transform: translateY(0px); }
    @media (min-width: 768px) {
      .lazy-load[data-direction="ltr"] {
        transform: translateX(-100px); }
        .lazy-load[data-direction="ltr"].lazy-loaded {
          transform: translateX(0px); } }
  .lazy-load[data-direction="rtl"] {
    transform: translateY(50px); }
    .lazy-load[data-direction="rtl"].lazy-loaded {
      transform: translateY(0px); }
    @media (min-width: 768px) {
      .lazy-load[data-direction="rtl"] {
        transform: translateX(100px); }
        .lazy-load[data-direction="rtl"].lazy-loaded {
          transform: translateX(0px); } }
  .lazy-load[data-direction="btt"] {
    transform: translateY(50px); }
    .lazy-load[data-direction="btt"].lazy-loaded {
      transform: translateY(0px); }
  .lazy-load[data-direction="ttb"] {
    transform: translateY(-50px); }
    .lazy-load[data-direction="ttb"].lazy-loaded {
      transform: translateY(0px); }
  .lazy-load[data-delay="200"] {
    transition-delay: 200ms; }
  .lazy-load[data-delay="400"] {
    transition-delay: 400ms; }
  .lazy-load[data-delay="600"] {
    transition-delay: 600ms; }
  .lazy-load[data-delay="800"] {
    transition-delay: 800ms; }
  .lazy-load[data-delay="1000"] {
    transition-delay: 1000ms; }
  .lazy-load.lazy-loaded {
    opacity: 1; }

.e-row {
  margin-left: -12px;
  margin-right: -12px;
  width: calc(100% + 12px * 2); }
  .e-row:after {
    content: '';
    clear: both;
    display: table; }

/*
	elements
*/
.e-logo {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 16px;
  left: 16px; }
  @media (min-width: 768px) {
    .e-logo {
      bottom: 20px;
      left: 20px; } }
  .e-logo img {
    width: 75%; }

/*
	modules
*/
.m-main-content {
  height: 100vh;
  padding: 10% 0 0 0; }

.m-container {
  position: relative;
  margin: 0 auto;
  max-width: 780px;
  width: 100%;
  padding: 0px 16px; }
  @media (min-width: 768px) {
    .m-container {
      padding: 0px 20px; } }

.m-location-block {
  background-color: #fff;
  box-shadow: 0px 10px 80px rgba(0, 0, 0, 0.1);
  font-weight: 500; }
  .m-location-block__image-wrap {
    position: relative;
    height: 120px; }
    @media (min-width: 768px) {
      .m-location-block__image-wrap {
        height: 180px; } }
    .m-location-block__image-wrap figure {
      position: absolute;
      left: 0;
      top: 0;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 100%;
      height: 100%; }
  .m-location-block__content-wrap {
    padding: 35px 50px 40px 35px; }
    .m-location-block__content-wrap:after {
      content: '';
      clear: both;
      display: table; }
  @media (min-width: 768px) {
    .m-location-block__col {
      float: left;
      width: 50%; }
      .m-location-block__col:last-child {
        display: flex;
        justify-content: flex-end; } }
  .m-location-block__title {
    font-size: 24px;
    color: #111111;
    margin: 0 0 15px 0;
    text-transform: uppercase;
    letter-spacing: 1.6px; }
  .m-location-block__phone {
    font-size: 16px;
    color: #D7D7D7;
    font-weight: 700; }
  .m-location-block__adress {
    list-style-type: none;
    padding: 0px;
    margin: 25px 0 40px 0; }
    .m-location-block__adress li {
      font-size: 14px;
      color: #aaaaaa;
      margin: 0 0 5px 0; }
  .m-location-block__maps {
    text-decoration: none;
    font-size: 12px;
    text-transform: uppercase;
    color: #65708F;
    padding: 0 0 6px;
    border-bottom: 2px solid rgba(101, 112, 142, 0.2);
    font-weight: 700; }
  .m-location-block__opening-hours {
    list-style-type: none;
    padding: 30px 0 0 0; }
    .m-location-block__opening-hours:after {
      content: '';
      clear: both;
      display: table; }
    .m-location-block__opening-hours li {
      display: block;
      width: 100%;
      display: flex;
      justify-content: space-between;
      height: 18px; }
  .m-location-block__opening-day {
    font-size: 13px;
    color: #999;
    text-transform: uppercase;
    display: inline-block;
    width: 50px; }
  .m-location-block__opening-time {
    font-size: 13px;
    color: #666;
    text-transform: uppercase;
    display: inline-block; }
    .m-location-block__opening-time span {
      display: inline-block;
      margin: 0 5px; }

.m-instagram {
  padding: 48px 0;
  min-height: 300px; }
  .m-instagram:after {
    content: '';
    clear: both;
    display: table; }
  .m-instagram img {
    float: left;
    width: 100%; }
    @media (min-width: 768px) {
      .m-instagram img {
        width: calc(100% / 3); } }

.m-links {
  padding: 24px 0 48px; }
  .m-links ul {
    width: 100%;
    text-align: center; }
    .m-links ul li {
      display: inline-block;
      padding: 0 24px; }
    .m-links ul a {
      border-bottom: 2px solid rgba(57, 87, 154, 0.2);
      text-decoration: none;
      font-size: 12px;
      text-transform: uppercase;
      color: #666;
      padding: 0 0 6px 0;
      font-weight: 700; }
